.app {
  min-height: 100vh;
  padding: 20px;
  background: #f5f5f5;
}

.container {
  max-width: 600px;
  margin: 0 auto;
}

.form {
  background: #fff;
  padding: 15px;
  border-radius: 12px;
  margin-bottom: 20px;
}

.title {
  text-align: center;
  color: #333;
  margin-bottom: 15px;
  font-family: Arial, sans-serif;
  font-size: 2rem;
  font-weight: bold;
  padding: 5px;
}

.input {
  padding: 8px;
  margin-bottom: 8px;
  border: 1px solid #ddd;
  border-radius: 8px;
  width: 100%;
  font-size: 16px;
}

.file-input {
  padding: 8px;
  margin-bottom: 8px;
  border: 2px dashed #4a8fd970;
  border-radius: 8px;
  width: 100%;
  font-size: 16px;
  background: #f0f7ff;
  cursor: pointer;
}

.file-input::file-selector-button {
  padding: 5px 10px;
  background: #4a90d9;
  color: #fff;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  margin-right: 10px;
}

.btn {
  padding: 8px 15px;
  background: #4a90d9;
  color: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  width: 100%;
}

.book-list {
  list-style: none;
  padding: 0;
}

.book-item {
  background: #fff;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.book-img {
  width: 50px;
  height: 70px;
  object-fit: cover;
  border-radius: 4px;
  background: #ddd;
}

.book-title {
  flex: 1;
  font-size: 18px;
  color: #333;
}

.delete-btn {
  padding: 5px 10px;
  background: #e74c3c;
  color: #fff;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.book-item input[type="checkbox"] {
  width: 20px;
  height: 20px;
  cursor: pointer;
  accent-color: #4a90d9;
}

.read-count {
  text-align: center;
  margin-top: 20px;
  color: #666;
}

@media (max-width: 768px) {
  .app {
    padding: 15px;
  }
}

@media (max-width: 480px) {
  .app {
    padding: 10px;
  }
  .form {
    padding: 10px;
  }
  .title {
    font-size: 18px;
  }
  .book-item {
      flex-direction:row;
      align-items: center;
      gap: 18ypx;
  }
  .book-img {
    width: 40px;
    height: 50px;
    margin-bottom: 5px;
  }
  .btn {
    width: 100%;
  }
}
